body#projets {
  @import url("https://fonts.googleapis.com/css?family=Exo+2:900");
}
body#projets #item-0 {
  grid-row-start: 1;
  grid-column-start: 1;
  grid-row-end: 2;
  grid-column-end: 3;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 10px;
  flex-direction: column;
}
body#projets #item-0 h2 {
  font-size: 20px;
  color: black;
}
body#projets #item-0 p {
  display: flex;
  justify-content: space-evenly;
  font-size: 15px;
  color: white;
  max-width: 100%;
}
body#projets #item-1 {
  grid-row-start: 2;
  grid-column-start: 1;
  grid-row-end: 3;
  grid-column-end: 2;
}
body#projets .projet1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 19vh;
  width: 100%;
}
body#projets .projet1 img {
  max-width: 100%;
  height: auto;
  display: block;
  margin: auto;
}
body#projets #item-2 {
  grid-row-start: 2;
  grid-column-start: 2;
}
body#projets .descriptionp1 {
  padding: 15px;
}
body#projets img {
  background: red;
}
body#projets .liensduprojet {
  margin: 30px auto;
  width: 100%;
  text-align: center;
  max-width: 100%;
  box-sizing: border-box;
}
body#projets .projet1 > div {
  width: 100%;
  max-width: 100%;
}
body#projets h1,
body#projets p {
  margin: 0;
  padding: 0;
}
@keyframes up {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-2048px);
  }
}
@keyframes wobble {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
}
body#projets body {
  margin: 0;
  padding: 0;
  font-size: 16px;
}
body#projets h1 {
  font-family: "Exo 2", sans-serif;
  font-size: 50px;
  color: white;
  text-shadow: 0px 4px 48px rgba(255, 255, 255, 0.2);
  text-align: center;
  padding: 10px;
}
body#projets .container::after {
  content: "";
  position: absolute;
  top: 92px;
  left: 0;
  width: 100%;
  height: 87%;
}
body#projets .content {
  position: relative;
  z-index: 2;
  text-align: center;
  padding-top: 30px;
}
body#projets .content h1 {
  color: white;
  font-size: 36px;
}
body#projets .presentationmoi {
  font-size: 20px;
  margin-bottom: 48px;
  padding: 16px;
}
@media screen and (min-width: 1024px) {
  body#projets h1 {
    font-size: 20px;
    font-family: "Exo 2", sans-serif;
    font-size: 90px;
    color: white;
    text-shadow: 0px 4px 48px rgba(255, 255, 255, 0.2);
    display: flex;
    align-content: center;
    flex-wrap: wrap;
  }
  body#projets .presentationmoi {
    font-size: 30px;
    display: flex;
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: 48px;
    padding: 0px 40px 0px 40px;
    flex-direction: column;
  }
  body#projets #item-0 h2 {
    font-size: 40px;
  }
  body#projets #item-0 p {
    font-size: 30px;
    max-width: initial;
  }
  body#projets #item-0 {
    padding: initial;
  }
  body#projets .projet1 {
    min-width: initial;
  }
  body#projets img {
    background: initial;
  }
}